/* Global Plugins */
.headroom {
  transition: transform .4s ease-in-out;
  animation-fill-mode: none;
}

.headroom.headroom--not-top {
  background-color: white;
}

/* Global Partials */
input[type="text"],
input[type="submit"],
textarea {
  -webkit-appearance: none;
  -webkit-border-radius: 0px;
}

::-webkit-input-placeholder {
  color: #444 !important;
}

:-moz-placeholder {
  color: #444 !important;
}

::-moz-placeholder {
  color: #444 !important;
}

:-ms-input-placeholder {
  color: #444 !important;
}

/* Global Body */
.break-word {
  display: inline-block;
  word-wrap: break-word;
}

/* Global Styles */
.theme-light {
  background: white;
}

.theme-dark {
  background: #696969;
}

.bg-light-grey {
  background-color: #E6E6E6;
}

img {
  max-width: 100%;
}

/* Force table to not be like tables anymore */
@media only screen and (max-width: 767px) {
  .table-responsive {
    border: none;
    /* Hide table headers (but not display: none;, for accessibility) */
    /* Label the data. Scope AND change these */
  }
  .table-responsive table, .table-responsive thead, .table-responsive tbody, .table-responsive th, .table-responsive td, .table-responsive tr {
    display: block;
  }
  .table-responsive thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }
  .table-responsive tr {
    padding: 15px 0;
  }
  .table-responsive td {
    /* Behave  like a "row" */
    padding-top: 5px !important;
    padding-bottom: 5px !important;
    padding-left: 50% !important;
    position: relative;
    border: none;
  }
  .table-responsive td:before {
    /* Now like a table header */
    position: absolute;
    /* Top/left values mimic padding */
    top: 5px;
    left: 0;
    width: 45%;
    white-space: nowrap;
    text-align: right;
  }
  .table-responsive td:nth-of-type(1):before {
    content: "Year";
  }
  .table-responsive td:nth-of-type(2):before {
    content: "Payments";
  }
  .table-responsive td:nth-of-type(3):before {
    content: "Interest";
  }
  .table-responsive td:nth-of-type(4):before {
    content: "Principal";
  }
  .table-responsive td:nth-of-type(5):before {
    content: "Ending Balance";
  }
}

